.contacts {
  &__map {
    height: 460px;
    margin-bottom: 33px;
  }

  &__form {
    max-width: 855px;
    padding-left: 30px;
    padding-bottom: 40px;

    & .form__heading {
      padding-left: 115px;
      margin-bottom: 38px;
    }

    .title.title-line:before {
      right: 105%;
    }

    .form__wrapper .form-group {
      margin: 0 40px 0 0;
      width: 41%;

      &:first-child {

        width: 36.2%;
      }

      .form-label {
        font-size: 26px;
      }
    }

    .form__wrapper .form-group:first-child .form-control {
      max-width: 100%;
    }
  }

  .form__wrapper {
    align-items: flex-end;
    flex-wrap: nowrap;
    max-width: 100%;
    margin: 0 0 10px 0;

    & button {
      font-size: 18px;
      font-weight: bold;
      padding: 5px;

      &:hover {
        color: $red;
      }
    }
  }

  .form__footer {
    justify-content: flex-end;

    & p {
      font-weight: normal;
      font-size: 12px;
    }
  }
}

@media (max-width: 1239px) {
  .contacts__form .form__heading {
    padding-left: 50px;
  }
  .contacts__form .title.title-line:before {
    right: 101%;
  }
  .form__heading .title {
    margin-bottom: 10px;
  }
  .contacts__form .form__heading {
    margin-bottom: 20px;
  }
  .contacts__form .form__wrapper .form-group .form-label {
    font-size: 20px;
  }

}

@media (max-width: 1023px) {
  .contacts__form .form__heading {
    padding-left: 0;
  }
}

@media (max-width: 767px) {
  .contacts .form__footer p {
    text-align: center;
  }
  .contacts__form .form__wrapper .form-group:first-child {
    width: 100%;
    margin-bottom: 15px;
  }
  .contacts__form .form__wrapper .form-group {
    width: 100%;
    margin: 0 0 15px 0;
  }
  .contacts .form__wrapper button {
    text-align: center;
  }
  .contacts .form__wrapper {
    text-align: center;
  }
  .contacts__form .title.title-line:before {
    right: 103%;
  }
  .contacts__form {
    padding-left: 0;
    max-width: 500px;
  }
  .contacts__form .form__wrapper .form-group .form-label {
    font-size: 18px;
  }
}