.list-page {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  & a {
    margin-bottom: 10px;

    &:hover {
      color: $red;
    }
  }
}

.btn-gradient {
  font-size: 15px;
  color: #ffffff;
  font-weight: bold;
  background: linear-gradient(104deg, rgb(254, 81, 81) 0%, rgb(255, 63, 63) 100%);
  box-shadow: 0px 4px 9.9px 0.1px rgba(255, 63, 63, 0.72);
  text-align: center;
  padding: 23px 27px;
  text-transform: uppercase;
  border-radius: 30px;

  &:hover {
    box-shadow: 0px 0px 0px 0.4px rgba(255, 63, 63, 0.72);
  }
}


.btn-border {
  border-width: 2px;
  border-color: rgb(255, 63, 63);
  border-style: solid;
  border-radius: 10px;
  font-size: 18px;
  color: #282828;
  padding: 9px 38px;
  text-align: center;
  font-family: 'Futura PT Demi';
  font-weight: 600;

  &:hover {
    background-color: rgb(255, 63, 63);
    color: $white;
  }
}


.heading {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;

  & h1 {
    margin-top: 10px;
    margin-bottom: 0;
  }

  &__item {
    display: flex;
    align-items: center;
    max-width: 210px;

    & span {
      font-size: 37px;
      margin-right: 23px;
      margin-top: 6px;
    }

    & p {
      letter-spacing: 0.7px;
      font-size: 14px;
      font-weight: 500;
      color: #2a2a29;
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      & a {
        display: block;
        color: $red;
        border-bottom: 1px solid $red;

        &:hover {
          border-bottom: 1px solid transparent;
        }
      }
    }

    &:last-child {
      max-width: 310px;

      & span {

      }
    }

  }

  &__items {
    max-width: 745px;
    padding-left: 66px;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto 12px auto;
    display: flex;
  }

  &__contacts {
    margin-bottom: 7px;
    justify-content: flex-end;
    display: flex;
    flex-direction: column;

    & .socials {
      display: flex;
      justify-content: flex-end;
      margin-right: -6px;


    }
  }

  &__tel {
    display: flex;
    align-items: center;
    font-size: 26px;
    font-weight: 900;
    color: #373737;
    white-space: nowrap;

    & span {
      margin-right: 12px;
      transition: none;
    }

    &:hover {
      & span {
        transition: none;
      }

      color: $red;
    }
  }

  &__callback {
    font-size: 39px;
    font-weight: 900;
    color: #ff3f3f;
    border-bottom: 2px solid #ff3f3f;
    margin-left: 91px;
    margin-right: 61px;
    margin-bottom: 18px;
  }
}

.breadcrumbs {
  margin-bottom: 25px;
  display: flex;
  margin-top: 3px;

  & span {
    font-size: 15px;
    font-family: 'Futura PT Cond';
    font-weight: 500;
    display: flex;
    color: #ff3f3f;

    & a {
      color: #282828;
      display: flex;

      &:after {
        content: '/';
        display: block;
        font-size: inherit;
        color: inherit;
        margin: 0 4px;
        transition: none;
      }

      &:hover {
        text-decoration: underline;
        color: $red;
      }
    }
  }
}

@media (max-width: 1439px) {
  .btn-border {
    font-size: 16px;
    padding: 5px 15px;
  }
  .heading__tel {
    font-size: 22px;
  }
  .heading__items {
    max-width: 645px;
  }
  .heading__callback {
    font-size: 30px;
    margin-left: 60px;
    margin-right: 0;
  }
}

@media (max-width: 1239px) {
  .heading__callback {
    margin-left: 25px;
  }
  .heading.heading-service .heading__items {
    max-width: 450px;
  }
  .heading.heading-service .heading__item:last-child {
    max-width: 210px;
  }
  .heading {
    align-items: center;
  }
  .btn-gradient {
    font-size: 14px;
  }
  .heading__tel {
    font-size: 20px;
  }
  .heading__items {
    max-width: 600px;
    padding-left: 20px;
  }
  .heading h1 {
    margin-top: 0;
  }
  .heading__item span {
    font-size: 27px;
    margin-right: 15px;
    margin-top: 2px;
  }
}

@media (max-width: 1023px) {
  .heading__callback {
    margin-left: auto;
    order: 2;
    font-size: 26px;
  }
  .heading.heading-service .heading__items {
    justify-content: center;
  }
  .heading.heading-service .heading__item:last-child {
    max-width: 310px;
  }
  .heading.heading-service .heading__items {
    max-width: 100%;
    margin-top: 15px;
  }
  .heading {
    flex-wrap: wrap;
    align-items: flex-start;
  }
  .heading__items {
    order: 3;
    width: 100%;
    padding-left: 0;
    max-width: 100%;
    justify-content: flex-end;
  }
  .heading__item {
    &:first-child {
      margin-right: 20px;
    }
  }
  .wrapper {
    padding-top: 55px;
  }
  .btn-gradient {
    padding: 15px 20px;
  }
  .heading__tel {
    font-size: 18px;
  }
  .breadcrumbs {
    margin-bottom: 15px;
  }
  .heading.heading-service {
    margin-bottom: 20px;
  }

}

@media (max-width: 767px) {
  .heading__tel {
    font-size: 16px;
  }
  .heading-service .heading__contacts {
    display: flex;
  }
  .heading__item p {
    font-size: 12px;
  }
  .heading__item span {
    font-size: 22px;
    margin-right: 10px;
    margin-top: 0;
  }
  .heading__items {
    display: none;
  }
  .heading__contacts {
    display: none;
  }
  .heading__callback {
    margin-bottom: 0;
  }
  .heading-service .heading__contacts .socials {
    justify-content: center;
  }
}

@media (max-width: 567px) {
  .heading.heading-service {
    @include verticalCenter;
  }
  .heading__callback {
    font-size: 22px;
    margin-left: 0;

  }
}