.portfolio {
  &__block {
    margin-bottom: 45px;
  }

  &__wp {
    display: flex;
    flex-wrap: wrap;
    margin: -20px -22px;
  }

  &__item {
    width: calc(33.3% - 44px);
    margin: 20px 22px;
    display: block;
    overflow: hidden;

    &:hover {
      & img {
        transform: scale(1.1);
        transition: .5s;
      }
    }

    &:before {
      content: '';
      display: block;
      padding-top: 50%;
    }

    & img {
      transition: .5s;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.pagination {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;

  & a {
    &:first-child, &:last-child {

      border-bottom: none;

      & span {
        color: #efefef;
        transition: .5s;
      }

      &:hover {
        border-bottom: none;

        & span {
          color: $red;
          transition: .5s;
        }
      }
    }

    &:first-child {
      margin-right: 25px;

      & span {
        transform: rotate(180deg);
      }
    }

    &:last-child {
      margin-left: 25px;
    }

    color: #ff3f3f;
    border-bottom: 1px solid transparent;
    font-size: 24px;
    margin: 0 2.5px;
    font-family: 'Futura PT Cond';

    &:hover {
      border-bottom: 1px solid #ff3f3f;

    }
  }

  .footing {
    padding-bottom: 70px;
  }
}

@media (max-width: 1439px) {
  .portfolio__item {
    width: calc(33.3% - 20px);
    margin: 10px;
  }
  .portfolio__wp {
    margin: -10px;
  }
}

@media (max-width: 1023px) {
  .portfolio__wp {
    margin: -5px;
  }
  .portfolio__item {
    width: calc(33.3% - 10px);
    margin: 5px;
  }
  .pagination {
    margin-top: 40px;
  }
  .portfolio__block {
    margin-bottom: 30px;
  }
}

@media (max-width: 767px) {
  .portfolio__item {
    width: calc(50% - 10px);
  }
  .pagination {
    margin-top: 30px;
  }
}