/*modal*/

.modal {
  display: none;
  max-width: 100%;
  overflow: visible;
  border-width: 1px;
  border-color: rgb(246, 246, 246);
  border-style: solid;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  width: 885px;
  padding: 60px 60px 60px 170px;

  & .title {
    margin-bottom: 18px;

    &:before {
      right: 104%;
    }
  }

  & .form-group {
    padding-left: 12px;
    margin-bottom: 35px;

    &.form-group-textarea {
      padding-left: 0;
      margin-bottom: 45px;
    }
  }

  & textarea.form-control {
    height: 135px;
  }

  .form__footer button {
    margin-right: 30px;
  }

}

.fancybox-close-small {
  opacity: 1;

}

.fancybox-slide--html .fancybox-close-small {
  right: -40px;
  top: -40px;
  color: #dcdcdc;
  padding: 5px;

  &:before {
    font-size: 31px;
    display: block;
    transition: none;
    content: "\e907";
    font-family: 'icomoon' !important;
  }

  & svg {
    display: none;
  }

  &:hover {
    color: $red;
  }
}

.fancybox-slide--html {
  padding: 40px;
}

@media (max-width: 1023px) {
  .modal {
    padding: 40px 40px 40px 80px;
  }
  .modal .form-group.form-group-textarea {
    margin-bottom: 20px;
  }
  .modal .form-group {
    margin-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .modal {
    padding: 30px;
  }
  .fancybox-slide--html {
    padding: 5px;
  }
  .fancybox-slide--html .fancybox-close-small {
    right: 0;
    top: -45px;
  }
}

@media (max-width: 567px) {
  .modal {
    padding: 20px 10px;
  }
  .modal .form-group {
    padding-left: 0;
  }
  .modal textarea.form-control {
    height: 100px;
  }
}