.footer {
  padding-top: 50px;
  border-top: 1px solid #ffebeb;

  &__logo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 32px;

    &_img {
      width: 305px;
      height: 55px;
      margin-bottom: 15px;

      & img {
        max-width: 100%;
        max-height: 100%;
      }
    }

    &_text {
      letter-spacing: 0.7px;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: 500;
      color: #2a2a29;
    }
  }

  &__info {
    margin-bottom: -9px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__block {
    max-width: 187px;
    width: 100%;

    & .socials {
      margin-left: -7px;
      margin-bottom: 23px;
    }
  }

  & p {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 900;
    color: #2a2a29;

    & span {
      margin-right: 18px;
      font-size: 30px;
      font-family: 'Futura PT Demi';

    }
  }

  &__item {
    display: flex;
    align-items: center;
    max-width: 210px;

    & span {
      font-size: 37px;
      margin-right: 23px;
      margin-top: 6px;
    }

    & p {
      letter-spacing: 0.7px;
      font-size: 14px;
      font-weight: 500;
      color: #2a2a29;
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      & a {
        display: block;
        color: $red;
        border-bottom: 1px solid $red;

        &:hover {
          border-bottom: 1px solid transparent;
        }
      }
    }

    &:last-child {
      max-width: 310px;

      & span {

      }
    }

  }

  &__items {
    max-width: 751px;
    padding-left: 71px;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
    display: flex;
  }

  &__tel {
    white-space: nowrap;
    font-size: 27px;
    font-weight: 900;
    color: #373737;

    &:hover {
      color: $red;
    }
  }

  &__wrap {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  &__dwnld {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    color: #2a2a29;

    & span {
      width: 33px;
      height: 33px;
      margin-right: 13px;
    }

    &:hover {
      color: $red;
    }
  }

  .wrap-nav {
    padding-left: 40px;

    & .nav-title {
      letter-spacing: 1.8px;
      font-size: 36px;
      font-weight: 900;
      color: #373737;
    }
  }

  & .menu {
    padding-left: 5px;
    display: flex;
    align-items: center;

    &-item {
      margin-right: 22px;
    }

    & a {
      letter-spacing: 0.7px;
      padding-bottom: 5px;
      font-size: 12px;
      text-transform: uppercase;
      color: #373737;
      font-weight: 900;

      &:after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        height: 2px;
        width: 0;
        background: $red;
        left: 0;
      }

      &:hover {
        &:after {

          width: 37px;
        }
      }
    }
  }

  &__callback {
    font-size: 40px;
    font-weight: 900;
    border-bottom: 1px solid $red;
    color: $red;

    &:hover {
      border-bottom: 1px solid transparent;
    }
  }

  &__btm {
    display: flex;
    align-items: center;
    margin-top: 45px;
    padding: 30px 0;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: -2000px;
      right: -2000px;
      top: 0;
      height: 1px;
      background: #ffebeb;
    }
  }

  &__note {
    font-size: 12px;
    font-weight: 400;
    color: #222222;

    & a {
      color: #ff0000;
      font-family: 'Futura PT Demi';

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: auto;

    & a {
      letter-spacing: -0.2px;
      font-size: 12px;
      text-decoration: underline;
      margin-bottom: 6px;
      color: #222222;

      &:last-child {
        margin-bottom: 0;
      }

      &:hover {
        text-decoration: none;
      }
    }
  }

  .socials {
    margin-left: -10.5px;
  }

}

@media (max-width: 1439px) {
  .footer__tel {
    font-size: 22px;
  }
  .footer__callback {
    font-size: 30px;
  }
  .footer .wrap-nav .nav-title {
    font-size: 28px;
  }
}

@media (max-width: 1239px) {
  .footer__logo_img {
    width: 255px;
    height: 45px;
    margin-bottom: 10px;
  }
  .footer__logo_text {
    font-size: 12px;
  }
  .footer {
    padding-top: 30px;
  }
  .footer__item:last-child {
    max-width: 240px;
  }
  .footer__items {
    padding-left: 0;
    max-width: 500px;
  }
  .footer .wrap-nav {
    padding-left: 0;
  }
  .footer .menu-item {
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
    }
  }
  .footer__btm {
    margin-top: 35px;
    padding: 20px 0;
  }
}

@media (max-width: 1023px) {
  .footer__logo {
    margin-bottom: 20px;
  }
  .footer .socials {
    margin-left: -6.5px;
  }
  .footer__block .socials {
    margin-bottom: 10px;
  }
  .footer__item span {
    font-size: 28px;
    margin-right: 15px;
    margin-top: 3px;
  }
  .footer__item p {
    font-size: 12px;
  }
  .footer__tel {
    font-size: 18px;
  }
  .footer .wrap-nav .nav-title {
    font-size: 22px;
  }
  .footer__callback {
    font-size: 25px;
  }
  .footer__btm {
    margin-top: 25px;
    padding: 10px 0;
  }
  .footer__info {
    margin-bottom: 15px;
    flex-wrap: wrap;
    justify-content: normal;
    align-items: center;
  }
  .footer__item {
    margin-right: 20px;
  }
  .footer__wrap {
    flex-wrap: wrap;
  }
  .footer .wrap-nav {
    width: 100%;
    order: 1;
    margin-bottom: 15px;
  }
  .footer__dwnld {
    order: 2;
  }
  .footer__callback {
    order: 3;
  }
  .footer .wrap-nav .nav-title {
    font-size: 20px;
  }
  .footer .menu {
    padding-left: 0;
  }
  .footer__items {
    margin-top: 10px;
    justify-content: normal;
    width: 100%;
    max-width: 100%;
    order: 3;
  }
  .footer p {
    font-size: 12px;
  }
  .footer p span {
    font-size: 20px;
    margin-right: 10px;
  }
  .footer__tel {
    order: 2;
    margin-left: auto;
  }
  .footer__block {
    order: 1;
  }
}

@media (max-width: 567px) {
  .footer__logo {
    @include verticalCenter;
  }
  .footer__logo_img {
    width: 220px;
    height: 40px;
    margin-bottom: 5px;
  }
  .footer__block .socials {
    margin-bottom: 5px;
  }
  .footer__logo_text {
    font-size: 10px;
  }
  .footer__items {
    @include verticalCenter;
  }
  .footer__item {
    margin-right: 0;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }
  .footer__block {
    max-width: 135px;
  }
  .footer__info {
    @include verticalCenter;
  }
  .footer__block {
    text-align: center;
  }
  .footer .socials {
    margin-left: 0;
  }
  .footer__block p {
    justify-content: center;
  }
  .footer__tel {
    margin-left: 0;
  }
  .footer__item {
    max-width: 240px;
  }
  .footer .menu {
    flex-wrap: wrap;
  }
  .footer .menu-item {
    margin-right: 0;
    width: 50%;
    padding: 0 5px;
    text-align: center;
  }
  .footer .wrap-nav .nav-title {
    text-align: center;
    margin-bottom: 5px;
  }
  .footer .wrap-nav {
    @include verticalCenter;
  }
  .footer__wrap {
    @include verticalCenter;
  }
  .footer__dwnld {
    margin-bottom: 10px;
  }
  .footer__btm {
    @include verticalCenter;
  }
  .footer__note {
    margin-bottom: 10px;
  }
  .footer__links {
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 0;
  }
}