.caption {
  display: flex;
  margin-bottom: 40px;
  align-items: center;

  &__text {
    & h2 {
      margin-bottom: 20px;
    }

    max-width: 953px;
    margin-right: 45px;

    & p {
      font-weight: 300;
      line-height: 143%;
      color: #282828;
    }
  }

  &__img {
    & img {
      width: 100%;
      height: 100%;
    }

    width: calc(34.4% - 45px);
    margin-left: auto;
  }

}

.about {
  & p {
    font-weight: 300;
    line-height: 143%;
    color: #282828;
  }

  &__gallery {
    display: flex;
    margin: -22.5px;
  }

  &__pic {
    width: calc(33.3% - 45px);
    margin: 22.5px;

    &:before {
      content: '';
      display: block;
      padding-top: 50.059%;
    }

    & img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__article {
    margin-top: 50px;

    & h2 {
      margin-bottom: 15px;
    }

    & p {
      margin-bottom: 30px;
    }
  }


}

.footing {
  padding-bottom: 105px;
  display: flex;
  align-items: center;

  &__text {
    max-width: 1125px;

    & h2 {
      margin-bottom: 14px;
    }

    & p {
      font-weight: 300;
      line-height: 143%;
      color: #282828;
    }
  }

  &__info {
    margin-left: auto;
    margin-top: 35px;
    padding-right: 28px;
  }

  &__tel {
    font-size: 39px;
    font-weight: 900;
    color: #373737;
    margin-bottom: 5px;

    & span {
      color: #ff3f3f;
    }

    &:hover {
      color: $red;
    }
  }
}

.start {
  &.start-note {
    padding: 80px 10px 68px 68px;
    align-items: flex-start;

    & .start__wp {
      justify-content: flex-start;
    }
  }

  padding: 89px 10px 85px 0;
  @include verticalCenter;

  &__wp {
    display: flex;
    align-items: center;

    &:hover {
      & .start__text {
        color: $red;
        border-bottom: 5px solid transparent;
      }

      & .icon-arrow-right {
        color: $red;
        transition: .2s;
      }
    }
  }

  &__icon {
    margin-right: 25px;
  }

  &__text {
    margin-top: 33px;
    font-size: 78px;
    font-weight: 900;
    color: #282828;
    border-bottom: 5px solid #282828;
  }

  & .icon-arrow-right {
    margin-top: 36px;
    margin-right: 50px;
    transition: .2s;
    font-size: 80px;
    color: #282828;
  }

  &__note {
    padding-top: 40px;
    font-size: 33px;
    font-weight: 900;
    color: #ff3f3f;
    margin-right: 50px;
  }
}

@media (max-width: 1639px) {
  .caption__text {
    max-width: 753px;
  }
  .footing__text {
    width: 100%;
    max-width: 100%;
  }
  .footing {
    flex-wrap: wrap;
    padding-bottom: 70px;
  }
  .footing__info {
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    padding-right: 0;
  }
}

@media (max-width: 1639px) {
  .start__text {
    font-size: 62px;
  }
}

@media (max-width: 1439px) {

  .start__note {
    font-size: 30px;
    margin-right: 30px;
  }

  .footing__tel {
    font-size: 30px;
  }

  .about__pic {
    width: calc(33.3% - 20px);
    margin: 10px;
  }

  .about__gallery {
    margin: -10px;
  }

  .start__text {
    font-size: 60px;
  }

  .start .icon-arrow-right {
    margin-top: 26px;
    margin-right: 20px;
    font-size: 60px;
  }

  .start {
    padding: 69px 10px 65px 0;
  }

}

@media (max-width: 1239px) {
  .start.start-note {
    padding: 49px 0 45px 0;
  }
  .start {
    padding: 49px 0 45px 0;
  }
  .caption__text {
    max-width: 653px;
  }
  .start__text {
    font-size: 52px;
  }
  .start__note {
    font-size: 26px;
    margin-right: 20px;
  }
  .start .icon-arrow-right {
    font-size: 50px;
  }
  .footing__tel {
    font-size: 26px;
  }
}

@media (max-width: 1023px) {
  .start__note {
    font-size: 22px;
  }
  .footing {
    padding-bottom: 40px;
  }
  .footing__tel {
    font-size: 22px;
  }
  .start.start-note {
    padding: 30px 0 30px 0;
  }
  .start {
    padding: 30px 0 30px 0;
  }
  .start__icon {
    width: 150px;
  }
  .start__text {
    font-size: 38px;
  }
  .caption {
    margin-bottom: 25px;
  }
  .caption {
    flex-wrap: wrap;
  }
  .caption__img {
    margin: 0 auto 10px auto;
    width: 100%;
    max-width: 100%;
  }
  .caption__text {
    max-width: 100%;
    order: 2;
    margin-right: 0;
  }
  .about__article {
    margin-top: 30px;
  }
  .about__article p {
    margin-bottom: 15px;
  }
  .start .icon-arrow-right {
    margin-right: 10px;
  }
  .start__note {
    padding-top: 20px;
  }
  .start__icon {
    margin-right: 15px;
  }
}

@media (max-width: 767px) {
  .about__gallery {
    flex-wrap: wrap;
    justify-content: center;
  }
  .about__pic {
    width: calc(50% - 10px);
    margin: 5px;
  }
  .about__article h2 {
    margin-bottom: 10px;
  }
  .start__icon {
    width: 100px;
  }
  .start__note {
    font-size: 20px;
  }

  .start__text {
    font-size: 28px;
    margin-top: 20px;
    border-bottom: 2px solid #282828;
  }
  .start .icon-arrow-right {
    font-size: 30px;
    margin-top: 20px;
  }
}

@media (max-width: 567px) {
  .start.start-note {
    align-items: center;
  }
  .start__note {
    padding-top: 0;
    margin-right: 0;
    font-size: 18px;
    margin-bottom: 15px;
  }
  .about__gallery {
    width: 100%;
    margin: 0;
  }
  .about__pic {
    width: 100%;
    margin: 0 0 10px 0;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
  .start__wp {
    @include verticalCenter;
  }
  .start .icon-arrow-right {
    margin: 0;
    transform: rotate(90deg);
  }
  .start__text {
    font-size: 20px;
    margin-top: 15px;
  }
  .start__icon {
    margin: 0 auto 15px auto;
  }
}