.services {
  &__wp {
    max-width: 1255px;
    padding: 0 0 0 43px;
    display: flex;
    flex-wrap: wrap;
    margin: -22px;
    justify-content: space-between;
  }

  &__item {
    margin: 22px 10px;
    max-width: 310px;
    width: 100%;
    padding: 12px 10px 21px 19px;
    border-width: 1px;
    border-color: rgb(223, 223, 223);
    border-style: solid;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    transition: .5s;

    & .service__title {
      transition: .5s;
    }

    &.services__item-link {
      border: none;
      background-color: transparent;
      box-shadow: none;

      &:hover {
        box-shadow: none;

        & .services__block {
          &:after {
            background-color: #ffffff;
            box-shadow: -14px -5px 30px -30px #000;
          }

          &:before {
            background-color: #ff3f3f;
            box-shadow: -14px -5px 30px -30px #000;
          }
        }
      }
    }

    &:hover {
      box-shadow: -14px -5px 30px -30px #000;
      transition: .5s;

      & .service__title {
        color: $red;
        transition: .5s;
      }
    }

  }

  &__link {
    display: block;
  }

  &__block {
    width: 96%;
    height: 235px;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 20px;
      right: -20px;
      bottom: -13px;
      top: 16px;
      z-index: -1;
      background-color: #ff3f3f;
      border-radius: 10px;
      transform: skewX(-3deg);
    }

    &:before {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(219, 219, 219);
      background-color: rgb(255, 255, 255);
      border-radius: 10px;
      transform: skewX(-3deg);
    }
  }

  .service__nav {
    margin-right: 0;
    padding-left: 0;
    width: 100%;

    & ul {
      & li {
        padding-left: 0;

        &:before {
          display: none;
        }
      }
    }
  }

  .service__title {
    font-size: 24px;
  }

  .footing {
    padding-bottom: 53px;
  }
}

@media (max-width: 1239px) {
  .services__item {
    max-width: 300px;
    margin: 10px;
  }
  .services__wp {
    padding-left: 0;
  }
  .services__wp {
    margin: -10px;
  }
  .services .service__title {
    font-size: 22px;
  }
}

@media (max-width: 1023px) {
  .services .service__title {
    font-size: 17px;
  }
  .services__item {
    max-width: 100%;
    width: calc(33.3% - 20px);
  }
  .services .service__nav {
    margin-bottom: 0;
  }
}

@media (max-width: 767px) {
  .services .service__title {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .services__wp {
    justify-content: center;
  }
  .services__item {
    width: calc(50% - 20px);

    &:last-child {
      width: 100%;
    }
  }

}

@media (max-width: 567px) {
  .services__item {
    width: 100%;
    margin: 0 0 10px 0;

    &:last-child {
      margin-bottom: 0;
    }
  }
  .services__wp {
    margin: 0;
  }
}