
input::-webkit-input-placeholder {
  color: $black;
}

input::-moz-placeholder {
  color: $black;
}

input:-moz-placeholder {
  color: $black;
}

input:-ms-input-placeholder {
  color: $black;
}

textarea::-webkit-input-placeholder {
  color: $black;
}

textarea::-moz-placeholder {
  color: $black;
}

textarea:-moz-placeholder {
  color: $black;
}

textarea:-ms-input-placeholder {
  color: $black;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
  transition: all .5s ease
}

input:focus::-moz-placeholder {
  color: transparent;
  transition: all .5s ease
}

input:focus:-moz-placeholder {
  color: transparent;
  transition: all .5s ease
}

input:focus:-ms-input-placeholder {
  color: transparent;
  transition: all .5s ease
}

textarea:focus::-webkit-input-placeholder {
  color: transparent;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease
}

textarea:focus::-moz-placeholder {
  color: transparent;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease
}

textarea:focus:-moz-placeholder {
  color: transparent;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease
}

textarea:focus:-ms-input-placeholder {
  color: transparent;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease
}


.form-group {
  display: flex;

}

textarea.form-control {
  padding: 10px;
  height: 134px;
  border: 1px solid #bdbdbd;
}

.form-label {
  margin-right: 10px;
  font-weight: 300;
  color: #000000;
  white-space: nowrap;
}

.form-control {
  border-bottom: 1px solid #cccccc;
  margin-bottom: 2px;
  font-size: 18px;
  font-family: 'Futura PT Book';
  color: #343434;

  &::placeholder {
    font-size: 18px;
    font-family: 'Futura PT Book';
    color: #343434;
  }
}


.fl_upld {
  margin: 12px 0 21px 0;

  &:hover {
    & label {
      color: $red;
      transition: .5s;
    }
  }
}

.fl_upld label, .form-field .fl_upld label {
  display: block;
  width: 100%;
  font-weight: 300;
  font-size: 16px;
  overflow: hidden;
  cursor: pointer;
  color: #000000;
  padding: 5px 10px 5px 32px;
  transition: .5s;

  &.file-choosen {
    opacity: 0;
  }
}

.fl_upld .fl_nm, .form-field .fl_upld .fl_nm {
  white-space: nowrap;
  font-weight: 300;
  font-size: 16px;
  position: absolute;
  top: 1px;
  left: 1px;
  bottom: 1px;
  right: 1px;
  border-radius: 3px;
  overflow: hidden;
  pointer-events: none;
  background-color: #fff;
  z-index: -1;
  color: #000000;
  padding: 5px 10px 5px 32px;
}

.fl_upld .fl_inp[type=file], .form-field .fl_upld .fl_inp[type=file] {
  display: none;
}

.form-field input:not([type=checkbox]):not([type=radio]), .form-field textarea {
  display: block;
  width: 100%;
  font-weight: 300;
  font-size: 13px;
  padding: 12px 15px;
  background: #fff;
  border-radius: 3px;
}


.fl_upld label:after, .form-field .fl_upld label:after {
  content: "\e905";
  position: absolute;
  top: 52%;
  left: -1px;
  width: 25px;
  height: 25px;
  font-size: 26px;
  margin-top: -15px;
  font-family: 'icomoon' !important;

  border-radius: 3px;
  transition: none;
  text-align: center;
}


fl_upld .file-choosen + .fl_nm, .form-field .fl_upld .file-choosen + .fl_nm {
  z-index: 2;
}

@media (max-width: 767px) {
  .form-control {
    font-size: 16px;

    &::placeholder {
      font-size: 16px;
    }
  }

}

@media (max-width: 567px) {
  textarea.form-control {
    height: 100px;
  }
}