.homescreen {
  padding: 85px 0 95px 0;

  &__text {
    padding: 0 20px 0 105px;
  }

  &__title {
    font-weight: bold;
    font-size: 100px;
    color: #282828;
    margin-bottom: 59px;
    line-height: 118%;

    & span {
      display: block;
    }
  }

  & p {
    font-weight: 100;
    font-size: 24px;
    color: #000000;
    margin-bottom: 72px;
    max-width: 1300px;
    padding-left: 6px;
    line-height: 120%;

    & span {
      font-weight: 400;
    }
  }

  &__tel {
    padding-left: 6px;
    display: flex;
    align-items: center;
    font-size: 60px;

    &_text {
      font-weight: bold;
      margin-right: 18px;

      & span {
        color: $red;
      }
    }

    & a {
      &:hover {
        color: $red;
      }
    }
  }
}


.reviews {
  padding: 78px 0 108px 0;

  &__title {
    padding-left: 33px;
    font-size: 36px;
    line-height: 30px;
    font-weight: 300;
    color: #222222;
    margin-bottom: 10px;
  }

  &__wp {
    margin: 0 -52.5px;
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    width: calc(33.3% - 65px);
    margin: 0 32.5px;
    border-top: 1px solid #edeeff;
    border-bottom: 1px solid #edeeff;
    padding: 39px 20px 39px 53px;

    &:last-child {
      padding-left: 73px;
    }
  }

  &__icon {
    height: 50px;
    margin-bottom: 52px;

    & img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__rate {
    display: flex;
    align-items: center;

    & p {
      font-size: 15px;
      font-weight: 500;
      margin-right: 15px;
    }
  }

  &__stars {
    & span {
      width: 16px;
      height: 16px;
    }
  }
}

.title {

}

.clients {
  padding-bottom: 91px;

  &__heading {
    padding-left: 22px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    & p {
      font-size: 14px;
      font-weight: 400;
      padding-left: 4px;
      color: #000000;
      letter-spacing: -0.3px;
    }
  }

  & .title {

    padding-bottom: 10px;
    margin-bottom: -1px;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 5px;
      width: 70%;
      height: 1px;
      background: #e9e9e9;
    }
  }

  &__wp {
    display: flex;
    flex-wrap: wrap;
    margin: -30px 0;
  }

  &__item {
    width: 20%;
    margin: 30px 0;
    height: 175px;
    @include verticalCenter;

    & img {
      max-width: 100%;
      max-height: 100%;
    }

    &:last-child {
      justify-content: flex-end;
      padding-bottom: 8px;
    }
  }
}

.services-block {
  padding-bottom: 166px;

  &__heading {
    padding-bottom: 500px;
    @media (min-width: 2000px) {
      padding-bottom: 600px;
    }
    @media (min-width: 2500px) {
      padding-bottom: 700px;
    }

    &_title {
      font-size: 200px;
      font-weight: 900;
      color: #eaeeef;
      padding-left: 50px;
    }

  }


  &__bg {
    z-index: 2;
    position: absolute;
    right: 0;
    top: 170px;
    width: 75.6%;
    overflow: hidden;
    border-radius: 60px 0 0 60px;

    &:before {
      content: '';
      display: block;
      padding-top: 33.7%;
    }

    & img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__wp {
    display: flex;
  }

  &__title {
    margin-left: -5px;
    font-size: 100px;
    font-weight: 900;
    padding-bottom: 17px;
    margin-bottom: 33px;
    border-bottom: 4px solid #ff3f3f;
    display: inline-block;
    color: #282828;
  }

  &__list {
    width: 50%;
    padding-right: 10px;
    display: flex;
    flex-wrap: wrap;
    margin: -17.5px -10px;
  }

  &__item {
    width: calc(50% - 20px);
    margin: 15px 10px;

    &:nth-child(2n) {
      & .btn-border {
        float: right;
        margin-right: 10px;
      }
    }
  }

  &__subtitle {
    padding-left: 20px;
    font-size: 24px;
    font-weight: 900;
    color: #282828;
    margin-bottom: 5px;
  }

  &__article {
    width: 50%;
    padding-left: 50px;
    margin-top: -5px;

    & p {
      margin-bottom: 32px;
      font-size: 18px;
      font-weight: 300;
      color: #000000;
      line-height: 144%;
    }

    & h2 {
      margin-bottom: 10px;
    }
  }

  &__pictures {
    display: flex;
    margin-bottom: 35px;
    justify-content: space-between;
  }

  &__brand {
    margin: -13px 12px 0 -21px;

    & h2 {
      padding-left: 13px;
      margin-bottom: 8px;
    }
  }

  &__pic {

  }

  &__img {
    margin-bottom: 13px;
    width: 100%;

    z-index: -1;
    border-radius: 15px;

    &:before {
      content: '';
      display: block;
      padding-top: 39%;
    }

    & img {
      border-radius: 15px;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      right: -10px;
      bottom: -10px;
      border-radius: 15px;
      background-image: linear-gradient(104deg, rgb(254, 81, 81) 0%, rgb(255, 63, 63) 100%);
      width: 100%;
      height: 100%;
      transform: skewX(-5deg);
      z-index: -1;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 300;
    color: #282828;

    &:before {
      display: table;
      content: '';
      clear: both;
    }


    & .btn-border {
      float: right;
      margin: 2px 17px 5px 15px;
    }
  }

  &__link {
    display: block;
    height: 223px;
    margin-right: -8px;
    transform: skewX(2deg);
    border-radius: 15px;
    z-index: 2;

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 15px;
      background-color: rgb(239, 239, 239);
      width: 100%;
      height: 100%;
      transform: skewX(-5deg);
      z-index: -2;
    }

    &_text {
      transition: .5s;
      position: absolute;
      left: 52%;
      top: 45%;
      transform: translate(-50%, -50%);
      font-size: 80px;
      color: rgb(224, 223, 223);
      font-weight: 900;
      text-shadow: 0px 0px 1px rgba(150, 150, 150, 1);
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      right: -16px;
      bottom: -18px;
      border-radius: 15px;
      background-image: linear-gradient(104deg, rgb(254, 81, 81) 0%, rgb(255, 63, 63) 100%);
      width: 100%;
      height: 100%;
      transform: skewX(-6deg);
      z-index: -3;
    }

    &:hover {
      & .services-block__link_text {
        color: $red;
        transition: .5s;
      }
    }
  }
}

.advantages {
  padding-bottom: 196px;

  & .title {
    margin-bottom: 30px;
    padding-left: 25px;
  }

  &__wp {
    display: flex;
    flex-wrap: wrap;
    margin: -30px -25px;
    padding-left: 50px;
    justify-content: space-between;
  }

  &__img {
    z-index: 2;
    border-radius: 15px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transform: skewX(-5deg);

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item {
    width: calc(33.3% - 50px);
    margin: 30px 25px;

    &:first-child {
      margin-left: -6px;
      width: calc(36% - 50px);
      z-index: 2;
      height: 165px;

      & img {
        z-index: 2;
        border-radius: 15px;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &:after {
        content: '';
        display: block;
        position: absolute;
        right: -10px;
        bottom: -10px;
        border-radius: 15px;
        background-image: linear-gradient(104deg, rgb(254, 81, 81) 0%, rgb(255, 63, 63) 100%);
        width: 100%;
        height: 100%;
        transform: skewX(-5deg);
        z-index: -1;
      }
    }

    &:nth-child(2) {
      width: calc(32.8% - 50px);
    }

    &:nth-child(3) {
      width: calc(32.8% - 50px);
    }

    &:last-child {
      @include verticalCenter;


      & a {
        font-size: 36px;
        font-weight: 900;
        color: $red;
        border-bottom: 2px solid $red;

        &:hover {
          border-bottom: 2px solid transparent;
        }
      }
    }
  }

  &__title {
    display: flex;
    align-items: center;
    color: #282828;
    font-size: 24px;
    font-family: 'Futura PT Book';

    & span {
      padding-left: 20px;
      font-size: 72px;
      margin-left: auto;
      font-family: 'Futura PT Extra';
      color: #e9e9e9;
      line-height: 70%;
    }
  }

  &__text {
    & p {
      font-size: 18px;
      color: #282828;
      font-weight: 300;
    }
  }
}

.article {
  padding-bottom: 80px;

  &__wp {
    padding-left: 117px;
  }

  & .title {
    margin-bottom: 36px;


  }

  & p {
    line-height: 26px;
    color: #282828;
    font-weight: 300;
    margin-bottom: 24px;

    & a {
      font-size: inherit;
      color: inherit;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }

    &:last-child {
      margin-top: 90px;
    }

    &:nth-of-type(4) {
      margin-top: 33px;
      margin-bottom: 50px;
    }
  }

  & h2 {
    margin-bottom: 11px;

    &:nth-of-type(2) {
      margin-top: 95px;
      margin-bottom: 18px;
    }
  }

  &__img {
    width: 41.5%;
    position: relative;
    float: right;
    margin: 42px 5px 50px 90px;
    display: block;

    &:before {
      content: '';
      display: block;
      padding-top: 100%;
    }

    & img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      right: 60px;
      bottom: 30px;
      border: 8px solid $red;
      left: -40px;
      top: -40px;
    }
  }
}

.form {
  padding-bottom: 71px;

  .title.title-line:before {
    right: 104.5%;
  }

  &__wp {
    max-width: 860px;
    padding-left: 145px;
  }

  &__heading {
    margin-bottom: 50px;

    & .title {
      margin-bottom: 18px;
    }

    & p {
      font-size: 24px;
      line-height: 26px;
      font-weight: 300;
      max-width: 530px;
      color: #282828;


      & a {
        font-weight: 500;
        color: $red;
        transition: .5s;
        border-bottom: 1px solid transparent;

        &:hover {
          border-bottom: 1px solid $red;
          transition: .5s;
        }
      }
    }
  }

  textarea.form-control {
    max-width: 654px;
  }

  &__title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
  }

  &__wrapper {
    max-width: 635px;
    display: flex;
    flex-wrap: wrap;
    margin: 25.5px -17.5px;

    & .form-group {
      width: calc(50% - 35px);
      margin: 19.5px 17.5px;

      &:first-child {
        width: 55%;

        & .form-control {
          max-width: 240px;
        }
      }
    }
  }

  &__footer {
    display: flex;

    & button {
      margin-right: 80px;
      font-weight: bold;
      text-transform: uppercase;
      color: #282828;
      padding: 10px 10px 10px 0;

      &:hover {
        color: $red;
      }
    }

    & p {
      font-weight: 300;
      color: #282828;
      max-width: 520px;

      & a {
        color: $red;
        text-decoration: underline;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}